<template>
<div class="contents-page">
  <header>
    <div class="breadcrumbs">
      <p class="title">{{setBlogTitle}}</p>
      <img src="http://ww1.sinaimg.cn/large/006aJ0icly1g03tzqgh4ij305k05k746.jpg" alt="" class="position">
    </div>

    <div class="nav">
      <span class="nav-icon"></span>
      <ul class="nav-list">
        <li
          v-for="(item, index) in navList"
          :key="index"
          @click="selectItem(item, index)"
        >
          {{item.title}}
        </li>
      </ul>
    </div>
    <div class="user-info">
      <router-link to="/"><img src="http://ww1.sinaimg.cn/large/006aJ0icly1g03tvetarmj31hc0u0qv5.jpg" alt="Cynthia" class="avatar"></router-link>
      <div class="info-text">
        <p class="title">Cynthia</p>
        <p class="des">我有一壶酒，足以慰风尘。</p>
      </div>
    </div>
    <!-- 分类 -->
    <div class="nav nav-subclass" v-if="navIndex === 1">
      <span class="nav-icon">{{setBlogTitle}}</span>
      <ul class="nav-list">
        <li
          v-for="(item, index) in classification"
          :key="index"
          @click="selectClassification(item, index)"
        >
          {{item}}
        </li>
      </ul>
    </div>
  </header>
  <main>
    <ul class="blog-list" v-if="navIndex < 2">
      <li class="blog-years">2019<span class="year-icon"></span></li>
      <li
        v-for="(item, index) in blogList"
        :key="index"
        class="blog-post"
      >
        {{item.time}}
        <span>{{item.title}}</span>
      </li>
    </ul>
    <!-- 关于 -->
    <ul class="about-list" v-if="navIndex === 2">
      <li class="info">
        <p>喜欢编程，喜欢睡懒觉，前端开发工程师。</p>
      </li>
      <li class="github">
        <p>Github <a href="https://github.com/Cynthia1911" target="_blank">@Cynthia1911</a></p>
      </li>
      <li class="address">
        <p>中国 · 北京</p>
      </li>
    </ul>
  </main>
</div>
</template>

<script>
export default {
  data () {
    return {
      blogTitle: ['博客', '分类', '关于', '搜索'],
      navList: [
        {
          title: '博客',
          url: ''
        },
        {
          title: '分类',
          url: ''
        },
        {
          title: '关于',
          url: ''
        },
        {
          title: '搜索',
          url: ''
        }
      ],
      navIndex: 0,
      classification: ['前端', '生活 (3)', '工具', '读书', '随笔'],
      label: ['CSS', 'Vue', 'JavaScript', 'git', 'less', '工具', '读书'],
      blogList: [
        {
          time: '01-31',
          title: '今天是2019年1月31日，农历腊月26'
        },
        {
          time: '02-01',
          title: '今天是2019年2月1日，农历腊月27'
        },
        {
          time: '02-12',
          title: '今天是2019年2月12日，农历正月初八，开工大吉！'
        }
      ]
    }
  },
  computed: {
    setBlogTitle () {
      return this.blogTitle[this.navIndex]
    }
  },
  methods: {
    // 点击导航
    selectItem (item, index) {
      // this.$router.push(item.link)
      this.navIndex = index
    }
  }
}
</script>

<style lang='less' scoped>
  * {
    margin: 0;
    padding: 0;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    li {
      list-style-type: none;
    }
  }
  .contents-page {
    font-size: 14px;
    color: rgb(105, 102, 102);
    width: 500px;
    margin: 60px auto 0;
    border-left: 4px solid #f9f9f9;

    header {
      .breadcrumbs{
        width: 100px;
        margin-left: -52px;
        text-align: center;
        .position {
          height: 28px;
          width: 28px;
          margin-top: 10px;
        }
      }
      .nav {
        width: 40px;
        position: relative;
        margin-top: 10px;
        margin-left: -22px;
        cursor: pointer;

        .nav-icon {
          display: block;
          width: 40px;
          height: 40px;
          line-height: 40px;
          transition-duration: .5s;
          background-image: url("http://ww1.sinaimg.cn/large/006aJ0icly1g03txji72tj305k05kt8q.jpg");
          background-repeat: no-repeat;
          background-size: 100%;
          cursor: pointer;
        }

        .nav-list {
          position: absolute;
          top: 3px;
          left: 0px;
          display: none;
          width: 0px;
          li {
            width: 35px;
            height: 35px;
            background: #cdcdcd;
            border-radius: 50%;
            margin: 0 4px;
            line-height: 36px;
            color: #fff;
            font-size: 12px;
            display: inline-block;
            // transition-duration: .5s;
            cursor: pointer;

            &:hover {
              background: #9c9c9c;
            }
          }
        }

        &:hover {
          .nav-list {
            display: block;
            width: 251px;
            transition-property: width;
            transition-duration: 2s;
            transform: all 2s;
          }
        }

        &.nav-subclass {
          width: 35px;
          margin-top: 40px;
          margin-bottom: 10px;
          color: #fff;
          font-size: 12px;

          .nav-icon {
            display: block;
            width: 35px;
            height: 35px;
            line-height: 35px;
            background: #cdcdcd;
            border-radius: 50%;
            cursor: pointer;
          }
          .nav-list {
            display: block;
            width: 260px;
            top: -16px;
            left: 48px;
            text-align: left;

            li {
              font-family: 'HoangYen11';
              width: auto;
              height: 30px;
              margin: 5px 8px;
              padding: 0 14px;
              color: #999;
              border-radius: 15px;
              background: #f6f6f6;

              &:hover {
                color: #555544;
              }
            }
          }
        }

      }
      .user-info {
        position: relative;
        margin-left: -31px;
        margin-top: 15px;
        display: flex;
        .avatar {
          height: 62px;
          width: 62px;
          border-radius: 50%;
        }
        .info-text {
          display: flex;
          flex-direction: column;
          justify-content: center;
          text-align: left;
          padding-left: 8px;
          .title {
            font-family:'Philing';
            font-size: 18px;
            margin-top: 10px;
          }
          .des {
            font-size: 14px;
            margin-top: 15px;
          }
        }
      }
    }

    main {
      position: relative;
      padding-top: 42px;
      text-align: left;
      .blog-list {
        li {
          &::before {
            content: ' ';
            position: absolute;
            top: 50%;
            left: -2px;
            width: 10px;
            height: 10px;
            margin-top: -6px;
            margin-left: -5px;
            border-radius: 50%;
          }
        }
        .blog-years {
          font-family: 'HoangYen11';
          font-size: 24px;
          padding-left: 18px;
          position: relative;
          display: flex;
          align-items: center;
          &::before {
            background: #e67e22;
          }
          .year-icon {
            height: 24px;
            width: 24px;
            margin-left: 10px;
            background-image: url("http://ww1.sinaimg.cn/large/006aJ0icly1g03twn8d7uj305k05kglm.jpg");
            background-repeat: no-repeat;
            background-size: 100%;
            display: inline-block;
          }
        }
        .blog-post {
          display: flex;
          align-items: center;
          position: relative;
          margin-top: 30px;
          padding-left: 18px;
          font-size: 14px;
          color: #cdcdcd;
          span {
            font-size: 18px;
            margin-left: 3px;
            color: #555544;
            cursor: pointer;
            &:hover {
              color: #e67e22
            }
          }
          &::before {
            background: #cdcdcd;
          }
        }
      }
      .about-list {
        li {
          font-family: 'HoangYen11';
          font-size: 16px;
          padding-left: 28px;
          position: relative;
          display: flex;
          align-items: center;
          margin-bottom: 40px;
          &::before {
            content: ' ';
            position: absolute;
            top: 50%;
            left: -15px;
            width: 25px;
            height: 25px;
            margin-top: -12px;
            background-repeat: no-repeat;
            background-size: 100%;
          }
        }
        .info {
          &::before {
            background-image: url("http://ww1.sinaimg.cn/large/006aJ0icly1g03ty9wirmj305k05kweh.jpg");
          }
        }
        .github {
          a {
            text-decoration: none;
            margin-left: 10px;
            color: #333;
          }
          &::before {
            background-image: url("http://ww1.sinaimg.cn/large/006aJ0icly1g03tz65b1mj305k05k3ye.jpg");
          }
        }
        .address {
          &::before {
            background-image: url("http://ww1.sinaimg.cn/large/006aJ0icly1g03tx81ds9j305k05kweh.jpg");
          }
        }
      }
    }
  }
</style>



