import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import blogContents from '@/components/blogContents'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/contents',
      name: 'blogContents',
      component: blogContents
    }
  ]
})



